/* width = 100% * number of tab-panels */

.tile--is-empty {
    padding: 0;
    background-color: #1f1f1f;
    border-left: 2px solid #151515;
    border-right: 2px solid #151515;
  }

.tile-group__item--is-stationary {
      -webkit-transform: translate(0) translateZ(0px);
              transform: translate(0) translateZ(0px);
    }

.tile-group__item--is-soft-left {
      -webkit-transform: translate(-25%) translateZ(0px);
              transform: translate(-25%) translateZ(0px);
    }

.tile-group__item--is-hard-left {
      -webkit-transform: translate(-50%) translateZ(0px);
              transform: translate(-50%) translateZ(0px);
    }

.tile-group__item--is-soft-right {
      -webkit-transform: translate(25%) translateZ(0px);
              transform: translate(25%) translateZ(0px);
    }

.tile-group__item--is-hard-right {
      -webkit-transform: translate(50%) translateZ(0px);
              transform: translate(50%) translateZ(0px);
    }

.tile-group__item--is-left-edge {
      -webkit-transition: -webkit-transform 400ms cubic-bezier(0, 0, 0.2, 1);
      transition: -webkit-transform 400ms cubic-bezier(0, 0, 0.2, 1);
      transition: transform 400ms cubic-bezier(0, 0, 0.2, 1);
      transition: transform 400ms cubic-bezier(0, 0, 0.2, 1), -webkit-transform 400ms cubic-bezier(0, 0, 0.2, 1);
      -webkit-transition-delay: 400ms;
              transition-delay: 400ms;
    }

.tile-group__item--is-right-edge {
      -webkit-transition: -webkit-transform 400ms cubic-bezier(0, 0, 0.2, 1);
      transition: -webkit-transform 400ms cubic-bezier(0, 0, 0.2, 1);
      transition: transform 400ms cubic-bezier(0, 0, 0.2, 1);
      transition: transform 400ms cubic-bezier(0, 0, 0.2, 1), -webkit-transform 400ms cubic-bezier(0, 0, 0.2, 1);
      -webkit-transition-delay: 400ms;
              transition-delay: 400ms;
    }

